.wrapper {
  width: 600px;
}

.cards_wrapper {
  height: calc(100vh - 250px);
  overflow: auto;
}

/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
  width: 8px; /* ширина для вертикального скролла */
  height: 8px;
  background-color: #2f2f37;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}
