.order_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 214px;
  background: #1c1c21;
  border-radius: 40px;
  margin-bottom: 24px;
  padding: 24px;
  box-sizing: border-box;
  cursor: pointer;
}

.order_number_container {
  display: flex;
  justify-content: space-between;
}

.ingredients_price_container {
  display: flex;
  justify-content: space-between;
}

.order_name {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.ingredients_container {
  display: flex;
  align-items: center;
  position: relative;
  height: 64px;
}

.ingredient_image_box {
  width: 60px;
  height: 60px;
  overflow: hidden;
  position: absolute;
  border: 2px solid #5345f4;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ingredient_image {
  position: absolute;
  height: 60px;
  left: -30px;
  background: #131316;
}

.price_container {
  display: flex;
  align-items: center;
}

.icon_wrapper {
  display: flex;
  align-items: center;
}
