.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 272px;
  height: 208px;
  cursor: pointer;
  position: relative;
  border: 1px solid #131316;
}

.wrapper:hover {
  border: 1px solid #4c4cff;
}

.text_name {
  text-align: center;
}

.count {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4c4cff;
  border-radius: 50%;
  height: 32px;
  min-width: 32px;
}
