.page_wrapper {
  height: 100%;
  width: 100%;
  min-width: 1000px;
}

.content_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.blocks_container {
  min-width: 800px;
  display: flex;
}

.menu_container {
  width: 320px;
  min-width: 320px;
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 120px;
}

.input_container {
  height: 500px;
  width: 844px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 120px;
}

.buttons_container {
  display: flex;
  width: 480px;
  justify-content: space-between;
  margin-top: 30px;
}

.link {
  cursor: pointer;
}

.link:hover {
  color: #f2f2f3;
}
