.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  min-width: 720px;
  z-index: 2000;
}

.modal_container {
  width: 720px;
  background-color: #1c1c21;
  border-radius: 40px;
  border: 1px solid #e5e5e5;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  z-index: 9999;
}
